import EventEmitter from 'events';
import {Events} from '../pear';
import Substreams from './substreams'
const log = console.log;

class P2PScheduler extends EventEmitter {
    constructor(config) {
        super();

        this.p2pConfig = config;
        this.upstreamers = [];                            //
        this.downstreamers = [];                          //


        //
        this.expectedSeg = null;                          //type {sn: number, relurl: string}
        this.bufMgr = null;
        this.target = 0;                                  //

        //sn
        this._currPlaySN = -1;
        this.leadingCounter = 0;                          //

        //fastmesh
        this.substreams = new Substreams(config);                             //substreams

        this.totalUploadBW = config.defaultUploadBW;
        // this.streamingRate = 0;                           //bps
        if (config.transitionEnabled) this.tranInspectorId = this._setupTransInspector();
        this.grantAncestors = [];                         //
        this.transitioning = false;                       //
        this.transitionTries = 0;                         //
        this.targetAncestorId = '';                       //Id
    }

    load(context, config, callbacks) {

        console.warn(`[scheduler] substreams.length ${this.substreams.streamCount}`);
        if (this.substreams.requestFromP2P) {
            console.warn(`[scheduler] get data from data channel target ${this.substreams.target}`);
            this.currUpstreamer = this.substreams.currentStreamer;                                      //

            this.context = context;
            const frag = context.frag;
            this.expectedSeg = {sn: frag.sn, relurl: frag.relurl};
            log(`p2pLoader load ${frag.relurl} at ${frag.sn} from ${this.currUpstreamer.remotePeerId}`);
            // this.config = config;
            this.callbacks = callbacks;
            this.stats = {trequest: performance.now(), retry: 0, tfirst: 0, tload: 0, loaded: 0};
            this.retryDelay = config.retryDelay;
            let timeout = Math.min(this.p2pConfig.loadTimeout, config.p2pTimeout);             //p2p
            // setup timeout before we perform request
            this.requestTimeout = window.setTimeout(this._loadtimeout.bind(this), timeout*1000);
            this._loadInternal();
        } else {
            console.warn(`[scheduler] get data from xhrLoader target ${this.substreams.target} total ${this.substreams.totalStreams}`);
            context.frag.loadByXhr = true;
            this.sourceStreamer.load(context, config, callbacks);
            this.substreams.adjustTarget();
        }


    }

    abort() {
        log(`p2pLoader abort`);
        this.expectedSeg = null;
        //
        this.currUpstreamer.clearQueue();
    }

    destroy() {
        log(`p2pLoader destroy`);
        clearInterval(this.tranInspectorId);
    }

    _loadInternal() {

        // peer.clearQueue();                                               //
        //
        // peer.requestData(this.context.frag.relurl);
        // console.warn(`load ${msg.url} from peer ${peer.remotePeerId} targetIdx ${this.target}`);
        this.substreams.requestData(this.context.frag.relurl);
    }

    get hasUpstreamer() {
        return this.upstreamers.length > 0;
    }

    get hasDownstreamer() {
        return this.downstreamers.length > 0;
    }

    get residualBW() {                                         //
        return this.totalUploadBW - this.uploadSR;
    }

    get uploadSR() {                                           //streaming rate
        let sr = 0;
        for (let channel of this.downstreamers) {
            sr += channel.streamingRate;
        }
        return sr;
    }

    set currentPlaySN(sn) {
        this._currPlaySN = sn;
    }

    set bitrate(br) {
        this.substreams.bitrate = br;
    }

    _addUpStreamer(channel) {
        // this.upstreamers.unshift(channel);                    //
        this.upstreamers.push(channel);
        //
        console.warn(`${channel.remotePeerId}  ${channel.copys}`);
        this.substreams.addSubstreams(channel, channel.copys);

        this._setupChannel(channel);                          //
    }

    _loadtimeout() {
        log(`timeout while loading ${this.context.url}` );
        this.callbacks.onTimeout(this.stats, this.context, null);
        this.requestTimeout = null;
        this.leadingCounter = 0;
    }

    _addDownStreamer(channel) {
        this.downstreamers.push(channel);

        this._setupChannel(channel);                          //


    }

    addDataChannel(channel) {
        if (channel.isReceiver) {                        //
            this._addUpStreamer(channel);
        } else {
            this._addDownStreamer(channel);
        }
    }

    deleteDataChannel(channel) {
        log(`delete datachannel ${channel.channelId}`);
        if (channel.isReceiver) {
            this.substreams.deleteSubstream(channel.remotePeerId);
            this.upstreamers = this.upstreamers.filter(c => c !== channel);
        } else {
            this.downstreamers = this.downstreamers.filter(c => c !== channel);
        }
        // for (let i=0;i<this.downstreamers.length;++i){
        //     if (this.downstreamers[i] === channel) {
        //         this.downstreamers.splice(i, 1);
        //         return;
        //     }
        // }
        // for (let i=0;i<this.upstreamers.length;++i){
        //     if (this.upstreamers[i] === channel) {
        //         this.upstreamers.splice(i, 1);
        //         return;
        //     }
        // }
    }

    moveUpstreamsersToDown() {
        const msg = {
            event: Events.DC_DISPLACE
        };
        while (this.upstreamers.length > 0) {
            let streamer = this.upstreamers.pop();
            streamer.isReceiver = !streamer.isReceiver;
            this.downstreamers.push(streamer);
            streamer.send(JSON.stringify(msg));
        }
        this.substreams.clear();
    }

    clearUpstreamers() {
        for (let streamer of this.upstreamers) {
            let msg = {
                event: Events.DC_CLOSE
            };
            streamer.send(JSON.stringify(msg));
            streamer.destroy();
        }
        this.upstreamers = [];
        this.substreams.clear();
    }

    clearDownstreamers() {
        for (let i=0;i<this.downstreamers.length;++i){
            let streamer = this.downstreamers.pop();
            let msg = {
                event: Events.DC_CLOSE
            };
            streamer.send(JSON.stringify(msg));
            streamer.destroy();
        }
    }

    exchangeWithStreamser(streamer) {                                        //streamer
        const msg = {
            event: Events.DC_DISPLACE
        };
        if (streamer.isReceiver) {                                           //
            // this.upstreamers.splice(this.upstreamers.findIndex(item => item.remotePeerId === streamer.remotePeerId), 1);
            this.upstreamers = this.upstreamers.filter(item => item.remotePeerId !== streamer.remotePeerId);
            this.downstreamers.push(streamer);
            this.substreams.deleteSubstream(streamer.remotePeerId);
            msg.substreams = streamer.copys;                                 //
        } else {
            // this.downstreamers.splice(this.downstreamers.findIndex(item => item.remotePeerId === streamer.remotePeerId), 1);
            this.downstreamers = this.downstreamers.filter(item => item.remotePeerId !== streamer.remotePeerId);
            this.upstreamers.unshift(streamer);                             //
            this.substreams.addSubstreams(streamer, 1);
        }
        streamer.isReceiver = !streamer.isReceiver;
        streamer.send(JSON.stringify(msg));
    }

    clearAllStreamers() {
        this.clearUpstreamers();
        this.clearDownstreamers();
    }

    broadcastToUpstreamers(msg) {                                //
        for (let streamer of this.upstreamers) {
            streamer.send(JSON.stringify(msg))
        }
    }

    broadcastToDownstreamers(msg) {                              //
        for (let streamer of this.downstreamers) {
            streamer.send(JSON.stringify(msg))
        }
    }

    _setupChannel(channel) {

        // channel.once('close', () => {
        //
        //     log(`datachannel closed ${channel.channelId} `);
        //     // this.emit(Events.SIG_DCCLOSED, datachannel);
        //     this.deleteDataChannel(channel);
        //     channel.destroy();
        // })

        //upstreamer
        channel.on(Events.DC_REQUEST, msg => {
            const {sn, url} = msg;
            log(`receive request sn ${sn} url ${url} from ${channel.remotePeerId}`);
            if (this.bufMgr) {
                let seg;
                if (this.bufMgr.hasSegOfURL(url)) {                          //url
                    seg = this.bufMgr.getSegByURL(url);
                }
                if (seg) {
                    log(`bufMgr found seg sn ${sn} url ${seg.relurl}`);
                    let payload = seg.data,                                 //
                        dataSize = seg.size,                                //
                        packetSize = this.p2pConfig.packetSize,                     //
                        remainder = 0,                                      //
                        attachments = 0;                                    //
                    if (dataSize % packetSize === 0) {
                        attachments = dataSize/packetSize;
                    } else {
                        attachments = Math.floor(dataSize/packetSize) + 1;
                        remainder = dataSize % packetSize;
                    }
                    let response = {
                        event: Events.DC_PIECE,
                        attachments: attachments,
                        url: seg.relurl,
                        sn: seg.sn,
                        size: payload.byteLength
                    };
                    channel.send(JSON.stringify(response));
                    const bufArr = dividePayload(payload, packetSize, attachments, remainder);
                    for (let j=0;j<bufArr.length;j++) {
                        channel.send(bufArr[j]);
                    }
                    log(`datachannel send binary data total size ${payload.byteLength}`);

                    //streaming rate
                    channel.recordSended(response.size);

                } else {                                                                        //
                    let response = {
                        event: Events.DC_LACK,
                        url: url,
                        current: this._currPlaySN
                    };
                    channel.send(JSON.stringify(response));
                }
            }

        })
            .on(Events.DISPLACE, (msg) => {                                                        //
                const channelId = channel.channelId;
                channel.isReceiver = !channel.isReceiver;
                // this.downstreamers.splice(this.downstreamers.findIndex(item => item.channelId === channelId), 1);
                this.downstreamers = this.downstreamers.filter(item => item.channelId !== channelId);
                this.upstreamers.unshift(channel);
                this.substreams.addSubstreams(channel, msg.substreams);
                console.log(`datachannel ${channelId} displaced`);
            })
            .on(Events.DC_GRANT, msg => {
                if (msg.to_peer_id === this.peerId) {

                    this.grantAncestors.push(msg);

                } else {                                                          //
                    msg.TTL --;
                    this.broadcastToDownstreamers(msg);
                }
            })

        //dowmstreamer
        channel.on(Events.DC_RESPONSE, response => {         //response: {url: string, sn: number, payload: Buffer}
            log(`receive response sn ${response.sn} url ${response.url} size ${response.data.byteLength} from ${channel.remotePeerId}`);
            if (this.expectedSeg && response.url === this.expectedSeg.relurl && this.requestTimeout) {
                window.clearTimeout(this.requestTimeout);                            //
                this.requestTimeout = null;
                if (!this.stats.tload) {
                    let stats = this.stats;
                    stats.tload = Math.max(stats.tfirst, performance.now());
                    stats.loaded = stats.total = response.data.byteLength;
                    let onProgress = this.callbacks.onProgress;
                    if (onProgress) {
                        // third arg is to provide on progress data
                        onProgress(stats, this.context, null);
                    }
                }
                this.callbacks.onSuccess(response, this.stats, this.context);
            } else {                                                            //requestbuffer-manager
                if (this.bufMgr && !this.bufMgr.hasSegOfURL(response.url)) {
                    this.bufMgr.addBuffer(response.sn, response.url, response.data);
                }
            }
            // this.leadingCounter = 0;
            // log(`this.upstreamers.length ${this.upstreamers.length}`);
        })
            .on(Events.DC_REQUESTFAIL, (msg) => {                                  //
                // if (this.requestTimeout && this.upstreamers.length >= 2) {                                         //
                //     this.target = (this.target === this.upstreamers.total -1 ? 0 : this.target  + 1);
                //     log(`load one more time from ${this.upstreamers[this.target].remotePeerId}`);
                //     if (this.stats.retry <= 3) {                               //
                //         this._loadInternal();
                //         this.stats.retry ++;
                //     } else {
                //
                //     }
                // }

                // const upStreamerCurr = msg.current;                         //sn
                // if (this._currPlaySN - upStreamerCurr >= 2) {
                //     this.leadingCounter ++;
                //     if (this.leadingCounter === this.upstreamers.length) {    //
                //         // this.emit(Events.TRANSITION);
                //     }
                // } else {
                //     this.leadingCounter = 0;
                // }

            })
            .on(Events.DC_PIECE, () => {                                        //piecetfirst
                if (!this.stats.tfirst) {
                    this.stats.tfirst = Math.max(performance.now(), this.stats.trequest);
                }
            })
            .on(Events.DC_TRANSITION, (msg) => {
                if (msg.TTL > 0) {                                               //TTL0
                    msg.TTL --;
                    if (this.upstreamers.length > 0) this.broadcastToUpstreamers(msg);
                    if (msg.ul_bw > this.totalUploadBW) {                            //
                        let parents = [];                                //{peer_id, substreams}
                        for (let channel of this.upstreamers) {
                            parents.push({
                                peer_id: channel.remotePeerId,
                                substreams: channel.copys
                            });
                        }
                        const resp = {
                            event: Events.DC_GRANT,
                            delay: 0,                                                //
                            TTL: msg.source_TTL - msg.TTL,
                            parents: parents,
                            from_peer_id: this.peerId,
                            to_peer_id: msg.from_peer_id
                        };
                        channel.send(JSON.stringify(resp));                          //GRANT
                    }
                }
            })
    }

    _setupTransInspector() {                          //
        return window.setInterval(() => {
            if (this.residualBW > this.totalUploadBW/2 && this.upstreamers.length) {         //streaming rate  TODO
                const msg = {
                    event: Events.DC_TRANSITION,
                    ul_bw: this.totalUploadBW,                               //bps
                    TTL: this.p2pConfig.transitionTTL,
                    source_TTL: this.p2pConfig.transitionTTL,
                    from_peer_id: this.peerId
                };
                console.warn(`begin TRANSITION ${JSON.stringify(msg)}`);
                this.broadcastToUpstreamers(msg);
                window.setTimeout(() => {                                      //
                    console.log(`grantAncestors: ${JSON.stringify(this.grantAncestors)}`);
                    if (this.grantAncestors.length === 0) return;
                    this.grantAncestors.sort((a, b) => b.delay - a.delay);     //delay
                    /*
                     {
                     event: 'GRANT'
                     delay: number             //source-to-end delay, level
                     TTL: number
                     parents: Array<peerId>
                     from_peer_id: string
                     to_peer_id: string
                     }
                     */
                    this.transitioning = true;
                    const ancestor = this.grantAncestors.pop();
                    this.targetAncestorId = ancestor.from_peer_id;
                    console.warn(`targetAncestorId: ${this.targetAncestorId}`)
                    if (ancestor.parents.length > 0) {                         //ancestor
                        this.emit(Events.CONNECT, ancestor.parents);
                    } else {                                                   //ancestor
                        this.emit(Events.ADOPT, ancestor.from_peer_id);
                    }
                    this.grantAncestors = [];                                   //

                }, this.p2pConfig.transitionWaitTime*1000)
                this.transitionTries ++;                                        //1
                if (this.transitionTries === this.p2pConfig.maxTransitionTries) {
                    window.clearInterval(this.tranInspectorId);
                }
            }
            // console.warn(`residualBW: ${this.residualBW}`)
        }, this.p2pConfig.transitionCheckInterval*1000)
    }
}

function dividePayload(payload, packetSize, attachments, remainder) {
    let bufArr = [];
    if (remainder) {
        let packet;
        for (let i=0;i<attachments-1;i++) {
            packet = payload.slice(i*packetSize, (i+1)*packetSize);
            bufArr.push(packet);
        }
        packet = payload.slice(payload.byteLength-remainder, payload.byteLength);
        bufArr.push(packet);
    } else {
        let packet;
        for (let i=0;i<attachments;i++) {
            packet = payload.slice(i*packetSize, (i+1)*packetSize);
            bufArr.push(packet);
        }
    }
    return bufArr;
}

export default P2PScheduler;


